export const getHolidays = () => {
  const christian = Object.entries(CHRISTIAN_HOLIDAYS).map(([date, name]) => {
    const key = moment(date).format("YYYY-MM-DD");
    return {
      date: key,
      name: name,
      type: "folk",
    };
  });
  console.log(JSON.stringify(christian));
  return christian;
};
